import React from 'react'
import Header from '../components/Header'
import AppMain from '../components/AppMain'
import DynamicScrollToTop from '../components/DynamicScrollToTop'
import Sommaire from '../data/sommaire_v2.json'
import { useParams } from 'react-router-dom'
import Sidebar from '../components/Sidebar'
import { HashLink as Link } from 'react-router-hash-link'


const PremierPasPage = () => {
    const {docuRef} = useParams()
    const docReference = Sommaire.findIndex( i => i.docRef === docuRef)
    const docSommaire = Sommaire[docReference].parties

    let sidebarList = {
        sidebarDocRef: docReference,
        sidebarDocSom: docSommaire
    }
    const scrollWithOffset = (el) => {
        const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
        const yOffset = -84; 
        window.scrollTo({ top: yCoordinate + yOffset, behavior: 'smooth' }); 
    }
    return (
        <div>
            <Header />
            <AppMain classname={"documentation uc"}>
                <Sidebar {...sidebarList}/>
                <div className="whiteBlock colContent">
                    <div className="row">
                        <div className="col-xl-7 content"> 
                            <div className="docTitle">{Sommaire[docReference].documentation}</div>   
                            <h1>Je vérifie le calcul de l’effectif de l’entreprise</h1>
                            <div className="intro">
                                <p>Connaître l’effectif de l’entreprise est essentiel pour les représentants du personnel ou les organisations syndicales. La raison&nbsp;? Cet effectif permet de déterminer les obligations de l’employeur en matière d’élections du comité social et économique, en matière de missions et de droits des élus du personnel, de désignation de représentants syndicaux, etc. L’effectif doit être communiqué par l’employeur, les représentants du personnel ayant un rôle de contrôle.</p>
                            </div>
                            <h2 id="comprendre">Comprendre</h2>
                            <h3>1) Le décompte</h3>
                            <p>L’employeur doit procéder au décompte de ses effectifs <strong>chaque mois.</strong> Il ne peut se contenter d’effectuer un décompte en fin d’année.</p>
                            <p>Le décompte est calculé en unité équivalent temps plein et non en unité personne physique, et il comprend bien entendu les salariés de l’entreprise (tant que leur contrat de travail reste en vigueur, du début de la période d’essai jusqu’au terme du préavis, et y compris les travailleurs à domicile ou les travailleurs détachés à l’étranger), mais aussi d’autres personnes amenées à travailler sur le site de l’entreprise.</p>
                            <div className="notezle">
                                <p>Sont exclus du décompte de l’effectif les mandataires sociaux (y compris ceux cumulant mandat et contrat de travail dans la même entreprise), les stagiaires, les apprentis pendant la durée de la période d’apprentissage, les contrats de professionnalisation pendant la durée de l’action de professionnalisation ou les contrats aidés pendant la durée d’attribution de l’aide financière.&nbsp;À compter du 1<sup>er</sup> janvier 2019, les salariés embauchés en contrat unique d’insertion doivent bien être pris en compte dans l’effectif de l’entreprise.</p>
                            </div>
                            <p>En additionnant les différents chiffres calculés comme indiqué ci-après, on obtient l’effectif mensuel de l’entreprise. Cet effectif est notamment communiqué aux élus du comité social et économique dans la base de données économiques et sociales et doit être indiqué aux organisations syndicales préparant la négociation du protocole d’accord préélectoral. Le même calcul doit être opéré <strong>au sein de chaque établissement</strong> lorsque des établissements distincts sont reconnus dans l’entreprise.</p>
                            <h3>2) Les salariés en CDI</h3>
                            <p>En pratique, pour le calcul de l’effectif d’un mois donné, chaque salarié embauché en contrat de travail à durée indéterminée à temps complet toujours présent dans l’entreprise en fin de mois compte pour 1, peu importe qu’il soit arrivé dans l’entreprise en cours de mois. S’il n’est plus présent en cours de mois, il ne compte pas dans les effectifs. S’il travaille à temps partiel à 50 %, il compte pour 0,5. On calcule alors en divisant l’horaire inscrit dans le contrat de travail par la durée collective du travail dans l’entreprise.</p>
                            <h3>3) Les salariés en CDD, intérimaires et travailleurs mis à disposition</h3>
                            <p>Le même calcul doit être effectué pour les salariés embauchés en <strong>contrat à durée déterminée,</strong> sauf les salariés embauchés pour un remplacement qui ne sont jamais intégrés dans le calcul des effectifs. Ainsi, un salarié embauché en CDD pour accroissement d’activité à temps plein présent en fin de mois compte pour 1.</p>
                            <p><strong>Les intérimaires</strong> mis à disposition au sein de l’entreprise sont comptabilisés dans l’effectif de l’entreprise d’accueil sauf s’ils y remplacent un salarié absent. Le calcul est identique à celui prévu pour les salariés en CDD. À noter qu’ils sont également pris en compte dans l’effectif de l’entreprise de travail temporaire sous réserve d’avoir effectué au moins trois mois de missions au cours de la dernière année civile.</p>
                            <p><strong>Les travailleurs salariés d’une entreprise mis à disposition</strong> dans une autre entreprise sont à comptabiliser dans l’effectif de l’entreprise d’accueil dès lors qu’ils sont présents dans les locaux de cette entreprise d’accueil depuis au moins 12 mois. Leur présence doit être permanente et non uniquement ponctuelle. Ils ne doivent pas être comptabilisés s’ils remplacent dans l’entreprise d’accueil un salarié absent. Le calcul est ensuite identique à celui prévu pour les salariés en CDD. À noter qu’ils sont également pris en compte dans l’effectif de leur entreprise d’origine.</p>
                            <p>Les salariés en CDD, les intérimaires et les travailleurs mis à disposition ne sont à comptabiliser pour l’effectif d’un mois donné qu’à «&nbsp;due proportion de leur temps de présence au cours des douze mois&nbsp;précédents&nbsp;». Aucune méthode n’est rendue obligatoire par la loi pour déterminer comment l’employeur doit procéder à ce calcul, notamment pour une personne n’ayant travaillé qu’une partie d’un des 12 derniers mois. Les élus ou les organisations syndicales peuvent donc interroger l’employeur pour connaître la méthode utilisée pour la prise en compte de ces personnes dans l’effectif au prorata de leur temps de présence sur les 12 mois précédents.</p>
                            <h2 id="point-clés-à-retenir">Point clés à retenir</h2>
                            <ul>
                                <li><strong>L'employeur a l'obligation de tenir à jour le registre du personnel dans lequel figurent le nom, le prénom, la nationalité, le sexe, la qualification professionnelle, l'emploi, la nature du contrat, les dates d'entrée et de sortie de l'établissement.</strong> La tenue de ce registre oblige donc l’employeur à connaître les effectifs de l'entreprise de manière permanente et à en apprécier les variations. Les élus du CSE disposent à tout moment du droit d’accès à ce registre, ce qui leur permet ainsi de vérifier le calcul des effectifs communiqué par l’entreprise.</li>
                                <li><strong>L’effectif communiqué par l’employeur ne correspond pas au nombre de personnes pouvant prétendre par exemple aux activités sociales et culturelles ni au nombre de personnes susceptibles d’être électrices pour la mise en place du CSE.</strong> L’effectif communiqué par l’employeur sert uniquement à répondre à certaines obligations légales imposées par le Code du travail. L’employeur n’a d’ailleurs pas l’obligation de transmettre aux élus ou aux organisations syndicales le détail nominatif des personnes comptabilisées dans l’effectif de l’entreprise.</li>
                                <li><strong>Pour connaître les obligations de l’entreprise en matière de cotisations sociales, l’employeur doit, là aussi, calculer l’effectif de l’entreprise.</strong> En fonction du franchissement ou non de certains seuils d’effectif, l’entreprise peut alors être exonérée du paiement de certaines cotisations sociales ou bénéficier de taux réduits. Le calcul de l’effectif suit des règles différentes en fonction des cotisations concernées, différentes aussi de celles prévues notamment en matière de seuil de mise en place du CSE décrites dans la présente fiche.</li>
                            </ul>
                            <h2 id="références-aux-textes-officiels">Références aux textes officiels</h2>
                            <p>C. trav.,&nbsp;art. <a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.2311-2" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 2311-2</a> (seuil d’effectif pour la mise en place du CSE), <a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.1111-2" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 1111-2,</a>&nbsp;<a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.1111-3" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 1111-3</a>&nbsp;et&nbsp;<a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.2301-1" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 2301-1</a> (modalités de calcul des effectifs), <a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.1251-54" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 1251-54</a> (spécificités du calcul de l’effectif dans les entreprises de travail temporaire), <a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.1221-13" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 1221-13</a> et <a href="https://www.editions-tissot.fr/webboxes/code-du-travail-new.aspx?article=MCAL.1221-15" onclick="window.open(this.href,'','resizable=no,status=no,location=no,toolbar=no,menubar=no,scrollbars=yes,width=700');return false;">L. 1221-15</a> (accès des élus du CSE au registre unique du personnel)</p>
                            <p>CSS, art. L. 130-1 (calcul de l’effectif Sécurité sociale)</p>
                            <p>Cass. soc., 14&nbsp;avril 2010, n°&nbsp;09-60.367 (prise en compte des travailleurs mis à disposition)</p>
                        </div>
                        <div className="col-xl-offset-1 col-xl-4">
                            <nav className="onThisPage">
                                <h3>Dans cette page</h3>
                                <ul>
                                    <li>
                                        <Link smooth to={`#comprendre`} scroll={el => scrollWithOffset(el)}>Comprendre</Link>
                                    </li>
                                    <li>
                                        <Link smooth to={`#point-clés-à-retenir`} scroll={el => scrollWithOffset(el)}>Points clés à retenir</Link>
                                    </li>
                                    <li>
                                        <Link smooth to={`#références-aux-textes-officiels`} scroll={el => scrollWithOffset(el)}>Références aux textes officiels</Link>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </AppMain>
            <DynamicScrollToTop />
        </div>
    );
}

export default PremierPasPage;