import React from 'react'
import { Link } from 'react-router-dom'

const UCList = (props) => {
    console.log(props)
    return (
        <ul className="ucList">
            {props.ucList.map((list, i) =>
            <li key={i} className={`itemUC ` + list.ucType}>
                <Link to={`/thematique/`+props.docuRef+`/`+props.partPath+`/`+list.ucType} className="flex-items-center">
                    <div className="itemUCOuter flex-items-center">
                        <div>
                            <div className="titleUC">{list.ucTitle}</div>
                            <div className="refInternet">{list.ucRef}</div>
                        </div>
                    </div>
                </Link>
            </li>
            )}
        </ul>
    );
}

export default UCList;